.row_poster{
    width: 50vw;
    object-fit: contain;
    max-height: 100px;
    transition: transform 450ms;
    margin-right: 10px;
}
.row-posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}
.row-posters::-webkit-scrollbar {
    display: none;
}
.row_poster:hover {
    transform: scale(1.5);
}
.row_posterLarge {
max-height: 250px;
}
.row_posterLarge:hover {
    transform: scale(1.3);
}
.row{
    margin-left: 20px;
    color: white;
}
.row h3{
    margin-top: 5px;
    font-size: 3vh;
}